<template>
  <div
    id="home"
    v-loading.fullscreen.lock="fullscreenLoading"
    element-loading-text="資料更新中..."
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="header">高雄水整合平台後端系統</div>
    <div class="content">
      <!-- 水庫 -->
      <div class="title">水庫</div>
      <el-button @click="getWaterpool_CTSIdata">水庫卡爾森指數記錄</el-button>
      <el-button @click="getWaterpool_detailSearch"
        >進階水庫監測站的卡爾森指數變化</el-button
      >
      <el-button @click="getWaterpool_station">測站位置</el-button>
      <!-- 環境水體 -->
      <div class="title">環境水體</div>
      <el-button @click="getEnvironment_allRiver('message')"
        >取得所有高雄的環境水體流域名稱</el-button
      >
      <el-button @click="getEnvironment_station">監測站位置</el-button>
      <el-button @click="getEnvironment_newData">目前最新汙染資訊</el-button>
      <el-button @click="getEnvironment_detailSearch"
        >主要河川歷史汙染資訊</el-button
      >
      <!-- 地下水 -->
      <div class="title">地下水</div>
      <el-button @click="getGroundWater_station('message')"
        >監測站位置</el-button
      >
      <el-button @click="getGroundWater_newData">目前最新檢測資料</el-button>
      <el-button @click="getGroundWater_detailSearch"
        >進階搜尋檢測資料</el-button
      >
      <!-- 下載 -->
      <div class="title">下載</div>
      <el-button @click="getGroundWater_originalStation()"
        >原始的地下水監測站</el-button
      >
      <el-button @click="getGroundWater_originalDetailSearch()"
        >原始的地下水監測站歷史檢測資訊</el-button
      >
      <el-button @click="getGroundWater_originalRiverRPI()"
        >原始的環境水體河川汙染狀態</el-button
      >
      <el-button @click="getEnvironment_originalStation()"
        >原始的環境水體監測站</el-button
      >
      <el-button @click="getEnvironment_originalRiverData()"
        >原始的環境水體河川汙染歷史資料</el-button
      >
      <el-button @click="getWaterpool_originalDetailSearch()"
        >原始的水庫監測站歷史監測</el-button
      >
      <el-button @click="getWaterpool_originalStation()"
        >原始的水庫監測站</el-button
      >
      <!-- <el-image :src="require('@/assets/img/logo.png')" class="logoImg">
      </el-image> -->
    </div>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";
export default {
  data() {
    return {
      fullscreenLoading: false,
      waterpool_detailSearchYear: 1000,
      environment_newDataYear: 1000,
      groundWater_newDataYear: 1000,
      environment_allRiver: [],
      groundWaterDis: [],
      groundWaterSite: [],
      environment_mainRiver: ["二仁溪", "高屏溪", "阿公店溪", "鳳山溪"],
    };
  },
  mounted() {
    this.waterpool_detailSearchYear = moment(new Date()).format("YYYY");
    this.groundWater_newDataYear = moment(new Date()).format("YYYY");
    this.environment_newDataYear = moment(new Date()).format("YYYY") - 1911; // 取得今年的民國年分
  },
  methods: {
    // 水庫卡爾森指數資訊
    async getWaterpool_CTSIdata() {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}waterPoolAPI/CTSIdata`, {
          river: "澄清湖",
          clean: true,
        })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
      axios
        .post(`${this.allURL}waterPoolAPI/CTSIdata`, {
          river: "鳳",
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.$message({ message: "資料已更新", type: "success" });
          this.fullscreenLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 水庫進階搜尋監測站卡爾森指數資訊
    async getWaterpool_detailSearch() {
      this.fullscreenLoading = true;
      for (let i = 2007; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break
        let data = { damName: "阿公店水庫", year: i };
        if (i === 2007) data.clean = true;
        await axios
          .post(`${this.allURL}waterPoolAPI/waterPooldetailSearch`, data)
          .then((res) => {
            console.log(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      }
      for (let i = 2005; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break
        await axios
          .post(`${this.allURL}waterPoolAPI/waterPooldetailSearch`, {
            damName: "澄清湖水庫",
            year: i,
          })
          .then((res) => {
            console.log(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      }
      for (let i = 2005; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break\
        let data = { damName: "鳳山水庫", year: i };
        if (i == this.waterpool_detailSearchYear) data.end = true;
        await axios
          .post(`${this.allURL}waterPoolAPI/waterPooldetailSearch`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.waterpool_detailSearchYear) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 水庫監測站資訊
    async getWaterpool_station() {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}waterPoolAPI/stationName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.$message({ message: "資料已更新", type: "success" });
          this.fullscreenLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 環境水體取得監測站位置
    async getEnvironment_station() {
      this.fullscreenLoading = true;
      await this.getEnvironment_allRiver("nomessage");
      for (let i = 0; i < this.environment_allRiver.length; i++) {
        let data = {
          river: this.environment_allRiver[i].riverName,
          from: this.environment_allRiver[i].from,
        };
        if (i === 0) data.clean = true;
        if (i == this.environment_allRiver.length - 1) data.end = true;
        await axios
          .post(`${this.allURL}environmentAPI/stationName`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.environment_allRiver.length - 1) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 環境水體取得所有流域
    async getEnvironment_allRiver(type) {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}environmentAPI/riverName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.environment_allRiver = res.data.river;
          console.log(this.environment_allRiver);
          if (type !== "nomessage") {
            this.$message({ message: "資料已更新", type: "success" });
            this.fullscreenLoading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 環境水體取得最新河川汙染狀態
    async getEnvironment_newData() {
      this.fullscreenLoading = true;
      await this.environment_checkData();
      for (let i = 0; i < this.environment_mainRiver.length; i++) {
        let data = {
          year: this.environment_newDataYear,
          river: this.environment_mainRiver[i],
        };
        if (i === 0) data.clean = true;
        if (i == this.environment_mainRiver.length - 1) data.end = true;
        await axios
          .post(`${this.allURL}environmentAPI/newestRiverData`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.environment_mainRiver.length - 1) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 查看看今年有沒有重要河川污染資料( 沒有的話 => 將查詢年份減一 )
    async environment_checkData() {
      await axios
        .post(`${this.allURL}environmentAPI/newestRiverData`, {
          year: this.environment_newDataYear,
        })
        .then(async (res) => {
          if (res.data.data.length === 0) {
            console.log("無資料");
            this.environment_newDataYear = this.environment_newDataYear - 1;
            await this.environment_checkData();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 環境水體取得主要河川汙染情況
    async getEnvironment_detailSearch() {
      this.fullscreenLoading = true;
      for (let i = 0; i < this.environment_mainRiver.length; i++) {
        let data = {
          river: this.environment_mainRiver[i],
        };
        if (i === 0) data.clean = true;
        if (i == this.environment_mainRiver.length - 1) data.end = true;
        await axios
          .post(`${this.allURL}environmentAPI/RPIdata`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.environment_mainRiver.length - 1) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 地下水取得監測站資訊
    async getGroundWater_station(type) {
      this.fullscreenLoading = true;
      this.groundWaterDis = [];
      this.groundWaterSite = [];
      await axios
        .post(`${this.allURL}groundWater/stationName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          res.data.siteData[0].forEach((item) => {
            this.groundWaterDis.push(item.name);
          });
          for (let i = 1; i < res.data.siteData.length; i++) {
            this.groundWaterSite.push(res.data.siteData[i].SiteName);
          }
          console.log(this.groundWaterDis);
          console.log(this.groundWaterSite);
          if (type !== "nomessage") {
            this.$message({ message: "資料已更新", type: "success" });
            this.fullscreenLoading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 地下水取得最新監測資訊
    async getGroundWater_newData() {
      this.fullscreenLoading = true;
      await this.groundWater_checkData();
      await this.getGroundWater_station("nomessage");
      for (let i = 0; i < this.groundWaterDis.length; i++) {
        let data = {
          dis: this.groundWaterDis[i],
          year: this.groundWater_newDataYear,
        };
        if (i === 0) data.clean = true;
        if (i == this.groundWaterDis.length - 1) data.end = true;
        console.log(data);
        await axios
          .post(`${this.allURL}groundWater/newestGroundData`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.groundWaterDis.length - 1) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 查看看今年有沒有最新地下水監測資料( 沒有的話 => 將查詢年份減一 )
    async groundWater_checkData() {
      await axios
        .post(`${this.allURL}groundWater/newestGroundData`, {
          dis: "鳳山區",
          year: this.groundWater_newDataYear,
        })
        .then(async (res) => {
          console.log(res.data);
          if (res.data.newestData.length === 0) {
            console.log("無資料");
            this.groundWater_newDataYear = this.groundWater_newDataYear - 1;
            await this.groundWater_checkData();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 地下水取得進階搜尋資訊
    async getGroundWater_detailSearch() {
      this.fullscreenLoading = true;
      await this.getGroundWater_station("nomessage");
      for (let year = 2014; year <= 10000; year++) {
        console.log(year);
        if (year > this.groundWater_newDataYear) break; // 若查詢的年份超過今年就break
        for (let i = 0; i < this.groundWaterSite.length; i++) {
          let data = {
            station: this.groundWaterSite[i],
            year: year,
          };
          if (year == 2014 && i == 0) data.clean = true;
          if (
            year == this.groundWater_newDataYear &&
            i == this.groundWaterSite.length - 1
          )
            data.end = true;
          await axios
            .post(`${this.allURL}groundWater/detailSearch`, data)
            .then((res) => {
              console.log(res.data);
              if (
                year == this.groundWater_newDataYear &&
                i == this.groundWaterSite.length - 1
              ) {
                this.$message({ message: "資料已更新", type: "success" });
                this.fullscreenLoading = false;
              }
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }
    },
    // 取得下載的地下水監測站
    async getGroundWater_originalStation() {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}download/download_groundWaterStationName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.$message({ message: "資料已更新", type: "success" });
          this.fullscreenLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得下載的地下水原始監測資訊
    async getGroundWater_originalDetailSearch() {
      this.fullscreenLoading = true;
      await this.getGroundWater_station("nomessage");
      for (let year = 2014; year <= 10000; year++) {
        console.log(year);
        if (year > this.groundWater_newDataYear) break; // 若查詢的年份超過今年就break
        for (let i = 0; i < this.groundWaterSite.length; i++) {
          let data = {
            station: this.groundWaterSite[i],
            year: year,
          };
          if (year == 2014 && i == 0) data.clean = true;
          if (
            year == this.groundWater_newDataYear &&
            i == this.groundWaterSite.length - 1
          )
            data.end = true;
          await axios
            .post(
              `${this.allURL}download/download_groundWaterAlldetailSearch`,
              data
            )
            .then((res) => {
              console.log(res.data);
              if (
                year == this.groundWater_newDataYear &&
                i == this.groundWaterSite.length - 1
              ) {
                this.$message({ message: "資料已更新", type: "success" });
                this.fullscreenLoading = false;
              }
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }
    },
    // 取得下載的環境水體的原始河川汙染資訊
    async getGroundWater_originalRiverRPI() {
      this.fullscreenLoading = true;
      for (let year = 100; year <= 10000; year++) {
        console.log(year);
        if (year > this.environment_newDataYear) break; // 若查詢的年份超過今年就break
        for (let i = 0; i < this.environment_mainRiver.length; i++) {
          let data = {
            river: this.environment_mainRiver[i],
            year: year,
          };
          if (year == 100 && i == 0) data.clean = true;
          if (
            year == this.environment_newDataYear &&
            i == this.environment_mainRiver.length - 1
          )
            data.end = true;
          await axios
            .post(`${this.allURL}download/download_newestRiverData`, data)
            .then((res) => {
              console.log(res.data);
              if (
                year == this.environment_newDataYear &&
                i == this.environment_mainRiver.length - 1
              ) {
                this.$message({ message: "資料已更新", type: "success" });
                this.fullscreenLoading = false;
              }
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }
    },
    // 取得下載的環境水體的原始監測站資訊
    async getEnvironment_originalStation() {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}download/download_environmentstationName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.$message({ message: "資料已更新", type: "success" });
          this.fullscreenLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取得下載的環境水體的原始河川汙染檢測資料
    async getEnvironment_originalRiverData() {
      this.fullscreenLoading = true;
      for (let i = 0; i < this.environment_mainRiver.length; i++) {
        let data = {
          river: this.environment_mainRiver[i],
        };
        if (i == 0) data.clean = true;
        if (i == this.environment_mainRiver.length - 1) data.end = true;
        await axios
          .post(`${this.allURL}download/download_AllRPIdata`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.environment_mainRiver.length - 1) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 取得下載的水庫進階搜尋監測站卡爾森指數資訊
    async getWaterpool_originalDetailSearch() {
      this.fullscreenLoading = true;
      for (let i = 2007; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break
        let data = { damName: "阿公店水庫", year: i };
        if (i === 2007) data.clean = true;
        await axios
          .post(`${this.allURL}download/download_detailSearch`, data)
          .then((res) => {
            console.log(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      }
      for (let i = 2005; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break
        await axios
          .post(`${this.allURL}download/download_detailSearch`, {
            damName: "澄清湖水庫",
            year: i,
          })
          .then((res) => {
            console.log(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      }
      for (let i = 2005; i <= 10000; i++) {
        if (i > this.waterpool_detailSearchYear) break; // 若查詢的年份超過今年就break\
        let data = { damName: "鳳山水庫", year: i };
        if (i == this.waterpool_detailSearchYear) data.end = true;
        await axios
          .post(`${this.allURL}download/download_detailSearch`, data)
          .then((res) => {
            console.log(res.data);
            if (i == this.waterpool_detailSearchYear) {
              this.$message({ message: "資料已更新", type: "success" });
              this.fullscreenLoading = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 取得下載的水庫監測站資訊
    async getWaterpool_originalStation() {
      this.fullscreenLoading = true;
      await axios
        .post(`${this.allURL}download/download_waterPoolstationName`, {
          clean: true,
          end: true,
        })
        .then((res) => {
          console.log(res.data);
          this.$message({ message: "資料已更新", type: "success" });
          this.fullscreenLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style lang="scss" scoped>
#home {
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: scroll;
  .header {
    height: 150px;
    background-color: #003377;
    color: white;
    font-size: 4rem;
    font-weight: bold;
    padding: 50px 0 20px 50px;
  }
  .content {
    padding: 15px 50px;
    .title {
      color: #aa7700;
      font-size: 2rem;
      font-weight: bold;
      margin-bottom: 20px;
    }
    .logoImg {
      /deep/.el-image__inner {
        max-height: 300px !important;
        max-width: 500px !important;
      }
      position: absolute;
      right: 5px;
      bottom: 0;
    }
    /deep/.el-button {
      background-color: #00aa88;
      color: white;
      font-size: 1.3rem;
      font-weight: bold;
      margin: 0 5px 20px 0;
      padding: 20px;
    }
    /deep/.el-button:hover {
      background-color: #00ddaa;
      color: white;
      font-size: 1.3rem;
      font-weight: bold;
      margin: 0 5px 20px 0;
      padding: 20px;
    }
  }
  @media (max-width: 450px) {
    .header {
      height: 120px;
      color: white;
      font-size: 2rem;
      padding: 10px 0 20px 20px;
    }
    .content {
      padding: 15px 20px;
      .title {
        font-size: 1.5rem;
        margin-bottom: 10px;
      }
      /deep/.el-button {
        font-size: 1rem;
        margin: 0 5px 10px 0;
        padding: 10px;
      }
      /deep/.el-button:hover {
        font-size: 1rem;
        margin: 0 5px 10px 0;
        padding: 10px;
      }
    }
  }
}
</style>